import "./index.scss";

import React from "react";

import { BoldLacaText } from "../../atoms";
import { ProgressItem } from "../../molecules";

const Progress = ({ progress, text, additionalClassname }) => {
  return (
    <div className={`progress-items--wrapper`}>
      <div className="progress-title">
        <BoldLacaText text={text} />
      </div>
      <div className={`progress-items ${additionalClassname || ""}`}>
        {progress?.map((item, index) => (
          <ProgressItem
            key={index}
            number={index}
            text={item}
            length={progress.length}
          />
        ))}
        <div className="progress-line" />
      </div>
    </div>
  );
};

export default Progress;
