import "./index.scss";

import React, { useEffect, useRef } from "react";
import { useWindowDimensions } from "../../../helpers/hooks";
import { Ellipse, RegularText } from "../../atoms";
import Typewriter from "typewriter-effect/dist/core";

const InviteText = () => {
  const typewriterRef = useRef();
  const { width } = useWindowDimensions();

  useEffect(() => {
    if (typewriterRef.current) {
      const typewriter = new Typewriter(typewriterRef.current, {
        strings: ["Hello", "World"],
        autostart: true,
        delay: 100,
      });

      typewriter
        .pauseFor(1500)
        .typeString(`Znamy <span>odpowiedzi</span> na Twoje pytania`)
        .start();
    }
  }, [typewriterRef]);

  return (
    <div className="invite--wrapper">
      <Ellipse
        additionalStyle={{
          position: "absolute",
          top: 162,
          left: -100,
          width: 300,
          height: 100,
          display: width > 1190 ? "none" : "block",
        }}
      />

      <h1
        ref={typewriterRef}
        className={"text-soon"}
        style={width > 1190 ? { color: "#FAFAFA" } : { color: "#000000" }}
      >
        Znamy <span>odpowiedzi</span> na Twoje pytania
      </h1>

      <div className="invite-subtitle">
        <RegularText
          text={"Szybka pomoc specjalisty jakiego szukasz"}
          additionalStyle={
            width > 1190 ? { color: "#FAFAFA" } : { color: "#070D15" }
          }
        />
      </div>
    </div>
  );
};

export default InviteText;
