import "./index.scss";

import React from "react";

import { Card } from "../../molecules";

const Cards = () => {
  return (
    <div className="cards--wrapper">
      <Card
        text={
          "Szybko umówisz się na konsultację - w pełni online, bez wykonywania telefonów"
        }
        link={require("../../../assets/vector/timer.svg").default}
      />
      <Card
        text={"Zyskasz dostęp do XX specjalistów z całej Polski"}
        link={require("../../../assets/vector/suitcase.svg").default}
      />

      <Card
        text={"Sprawdzisz opinie o wybranym doradcy"}
        link={require("../../../assets/vector/user-stars.svg").default}
      />
      <Card
        text={
          "Połączysz się z doradcą na naszej platformie, bez dodatkowych programów"
        }
        link={require("../../../assets/vector/monitor.svg").default}
      />

      <Card
        text={
          "Zyskasz poufność i bezpieczeństwo - wszystkie połączenia są szyfrowane"
        }
        link={require("../../../assets/vector/protection.svg").default}
      />
      <Card
        text={"Już na wstępie poznasz cenę"}
        link={require("../../../assets/vector/hand.svg").default}
      />
    </div>
  );
};

export default Cards;
