import "./index.scss";

import React from "react";

import grayDot from "../../../assets/vector/gray-dot.svg";
import purpleDot from "../../../assets/vector/purple-dot.svg";
import { useWindowDimensions } from "../../../helpers/hooks";
import { RegisterProgressItem } from "../../molecules";

const ProgressRegistration = ({
  progress,
  progressNumber,
  additionalStyle,
}) => {
  const { width } = useWindowDimensions();

  return (
    <>
      <div
        className="progress-registration-items--wrapper"
        style={additionalStyle}
      >
        {width > 516 ? (
          <div className="progress-registration-items">
            {progress?.map((item, index) => (
              <RegisterProgressItem
                progressNumber={progressNumber}
                number={index}
                text={item}
                length={progress.length}
              />
            ))}
          </div>
        ) : (
          progress.map((val, index) => (
            <img
              src={index === parseInt(progressNumber) ? purpleDot : grayDot}
              alt={"progress"}
              width={8}
              style={{ marginRight: 8 }}
            />
          ))
        )}
      </div>

      <p className={"progress-registration-items--responsive-login"}>
        Jeśli masz już konto <a>zaloguj się</a>
      </p>
    </>
  );
};

export default ProgressRegistration;
