import "./index.scss";

import React from "react";

import { Dot, RegularText, SquareImage } from "../../atoms";
import Stars from "../Stars";

const OpinionHeader = ({ name }) => {
  return (
    <div className="opinion-header--wrapper">
      <SquareImage />
      <div className="opinion-header-text">
        <RegularText text={name} additionalStyle={{ fontSize: 16 }} />
      </div>
      <Dot />
      <Stars stars={5} />
    </div>
  );
};

export default OpinionHeader;
