import "./index.scss";

import React from "react";

import { useWindowDimensions } from "../../../helpers/hooks";
import { Button } from "../../atoms";

const OptionChooser = ({ options, selected, setSelected }) => {
  const { width } = useWindowDimensions();

  return (
    <div className="option-chooser">
      {options.map(option => (
        <Button
          text={option.name}
          additionalStyle={{
            padding: "8px 40px",
            background:
              selected?.name === option.name ? "#5861D6" : "transparent",
            width: width <= 560 ? "100%" : "fit-content",
            color: selected?.name === option.name ? "#FFF" : "#5861D6",
            border: "3px solid #5861D6",
            marginRight: 32,
            marginBottom: 24,
            boxShadow:
              selected?.name === option.name ? "0px 0px 10px #5861D6" : "none",
          }}
          handleOnClick={() => setSelected(option)}
        />
      ))}
    </div>
  );
};

export default OptionChooser;
