import "./index.scss";

import React from "react";

import { Icon, Link } from "../../atoms";

const Info = ({ text, link, icon, additionalStyle }) => {
  return (
    <a src={link} className="info--wrapper" style={additionalStyle}>
      <div className="info-icon">
        <Icon link={icon} />
      </div>
      <div className="info-link">
        <Link link={link} text={text} />
      </div>
    </a>
  );
};

export default Info;
