import React, { useState } from "react";

import "./index.scss";

const FileInput = ({ label, additionalStyle, accept, setValue }) => {
  const [localValue, setLocalValue] = useState();

  return (
    <div className="file-input--wrapper" style={additionalStyle}>
      <span className={"file-input--label"}>{label}</span>
      <div className="file-input--target">
        {localValue ? (
          <p className={"file-input--target--label"}>{localValue.name}</p>
        ) : (
          <p className={"file-input--target--label"}>
            Wgraj z dysku<span>&nbsp;lub przeciągnij</span>
          </p>
        )}
        <input
          type={"file"}
          className={"file-input"}
          onChange={(e) => {
            setLocalValue(e.target.files[0]);
            setValue(e.target.files[0]);
          }}
          accept={accept}
        />
      </div>
    </div>
  );
};

export default FileInput;
