import "./index.scss";

import React from "react";

import { RegularText } from "../../atoms";
import { OpinionHeader } from "../../molecules";

const Opinion = ({ text, name }) => {
  return (
    <div className="opinion--wrapper">
      <OpinionHeader name={name} />
      <div className="opinion-text">
        <RegularText
          text={text}
          additionalStyle={{ textAlign: "initial", fontSize: 16 }}
        />
      </div>
    </div>
  );
};

export default Opinion;
