import "./index.scss";

import React from "react";

import { ProgressNumber, RegularText } from "../../atoms";

const ProgressItem = ({ number, text, length }) => {
  return (
    <div
      className={`progress-item ${number === 0 ? "progress-item--first" : ""} ${
        number === length - 1 ? "progress-item--last" : ""
      }`}
    >
      <ProgressNumber number={number + 1} />
      <div className="progress-text">
        <RegularText text={text} />
      </div>
    </div>
  );
};

export default ProgressItem;
