import "./index.scss";

import React from "react";

import { cn } from "../../../helpers/utils";

const Button = ({
  type,
  target,
  href,
  text,
  additionalStyle,
  handleOnClick,
  disabled,
  className,
}) => {
  return type === "link" ? (
    <a
      className={cn("button", className)}
      style={{
        ...additionalStyle,
        cursor: "pointer",
      }}
      href={href}
      target={target}
    >
      {text}
    </a>
  ) : (
    <button
      className={cn("button", className)}
      style={{
        ...additionalStyle,
        opacity: disabled ? 0.3 : 1,
        cursor: disabled ? "initial" : "pointer",
      }}
      onClick={handleOnClick}
      disabled={disabled}
    >
      {text}
    </button>
  );
};

export default Button;
