import React, { useState } from "react";

import flaggedIcon from "../../../assets/vector/purple-flag.svg";
import emptyIcon from "../../../assets/vector/empty-flag.svg";

import "./index.scss";
import { cn } from "../../../helpers/utils";

const CheckBox = ({ label, additionalStyle, setChecked, error }) => {
  const [localChecked, setLocalChecked] = useState(false);

  return (
    <div
      className="checkbox--wrapper"
      style={additionalStyle}
      onClick={() => {
        setChecked && setChecked((prev) => !prev);
        setLocalChecked((prev) => !prev);
      }}
    >
      <img
        src={localChecked ? flaggedIcon : emptyIcon}
        alt={"flag"}
        className={"checkbox"}
        width={25}
      />
      <p
        className={cn(
          "checkbox--label",
          error && "checkbox--label__error",
          localChecked && "checkbox--label__checked",
        )}
      >
        {label}
      </p>
    </div>
  );
};

export default CheckBox;
