import React from "react";
import { cn } from "../../../helpers/utils";

import "./index.scss";

const TextInput = ({ label, value, setValue, type, error }) => {
  return (
    <div className="text-input--wrapper">
      <span className={"text-input--label"}>{label}</span>
      <div className={cn("text-input--target", error && "text-input--target__error")}>
        <input type={type || "text"} className={"text-input"} value={value} onChange={setValue} />
      </div>
      {error && <p className="text-input--error">{error}</p>}
    </div>
  );
};

export default TextInput;
