import { Link } from "gatsby";
import React from "react";
import "./index.scss";

const LogoText = ({isWhite}) => (
    <Link to="/" className="logo-text--wrapper">
        <div style={{color: isWhite ? 'white' : '#070D15'}} className="logo-text">uni</div>
        <div style={{color: isWhite ? 'white' : '#5652C5'}} className="logo-text logo-text-symbol">§</div>
        <div style={{color: isWhite ? 'white' : '#070D15'}} className="logo-text">law</div>
    </Link>
)
export default LogoText;