import "./index.scss";

import React from "react";

import { LogoText, RegularText } from "../../atoms";
import { NavItemDark } from "../../molecules";

const RegistrationNavigation = () => {
  return (
    <div className="registration-navigation--wrapper">
      <LogoText />
      <div className="registration-navigation-text--wrapper">
        <div className="registration-navigation-text">
          <RegularText text={"Jeśli masz już konto"} />
        </div>
        <div className="registration-navigation-text registration-navigation-text--link">
          <NavItemDark
            text={"Zaloguj się"}
            link={"https://unilaw-app.nebucode.com/sign-in"}
          />
        </div>
      </div>
    </div>
  );
};

export default RegistrationNavigation;
