import "./index.scss";

import React from "react";

import { BoldLacaText } from "../../atoms";
import { UserImage } from "../index";

const ChooseItem = ({ link, isRight, text }) => {
  return (
    <div className={`choose-item--wrapper`}>
      <div className="choose-item-title">
        <BoldLacaText text={text} />
      </div>
      <UserImage link={link} isRight={isRight} />
    </div>
  );
};

export default ChooseItem;
