import "./index.scss";

import React, { useRef, useState } from "react";

import { Link } from "gatsby";

import cross from "../../../assets/vector/purple-cross.svg";
import sandwich from "../../../assets/vector/sandwich.svg";
import { useWindowDimensions } from "../../../helpers/hooks";
import { Button, Copyright } from "../../atoms";
import { BackButton, Info, Logo, NavItem, NavItemDark } from "../../molecules";

const RightNav = ({ isDark }) => {
  const { width } = useWindowDimensions();
  const [responsiveRequested, setResponsiveRequested] = useState(false);
  const [contactPanelRequested, setContactPanelRequested] = useState(false);
  const responsiveRef = useRef();

  if (width > 1190) {
    return (
      <div className="navigation--container">
        {isDark ? (
          <div className="navigation--wrapper">
            <NavItemDark text={"Home"} link={"/"} />
            <NavItemDark text={"Zostań doradcą"} link={"/become-counselor"} />
            <NavItemDark text={"ICO"} link={"/ico"} />
            <NavItemDark
              text={"Zaloguj się"}
              link={"https://unilaw-app.nebucode.com/sign-in"}
            />
          </div>
        ) : (
          <div className="navigation--wrapper">
            <NavItem text={"Home"} link={"/"} />
            <NavItem text={"Zostań doradcą"} link={"/become-counselor"} />
            <NavItem text={"ICO"} link={"/ico"} />
            <NavItem
              text={"Zaloguj się"}
              link={"https://unilaw-app.nebucode.com/sign-in"}
            />
          </div>
        )}

        <Link className="navigation-button--link" to="/register">
          <div className="navigation-button">
            <Button text={"Zarejestruj się"} additionalStyle={{ width: 183 }} />
          </div>
        </Link>
      </div>
    );
  } else {
    return (
      <div className="navigation--container">
        {/*<div*/}
        {/*  className={"navigation--responsive--background"}*/}
        {/*  style={{ display: responsiveRequested ? "block" : "none" }}*/}
        {/*  onClick={() => setResponsiveRequested(false)}*/}
        {/*/>*/}
        <div
          className={`navigation--responsive${
            responsiveRequested ? " active" : ""
          }${contactPanelRequested ? " contact" : ""}`}
          ref={responsiveRef}
        >
          <div>
            <div
              style={{
                display: "flex",
                justifyContent: "space-between",
                alignItems: "center",
              }}
            >
              {contactPanelRequested ? (
                <BackButton
                  handleOnClick={() => setContactPanelRequested(false)}
                  isDark
                />
              ) : null}

              <Logo withoutText />

              {!contactPanelRequested ? (
                <button
                  type={"button"}
                  className={"navigation--responsive--close-btn"}
                  onClick={() => setResponsiveRequested(false)}
                >
                  <img src={cross} alt={"close"} />
                </button>
              ) : (
                <button
                  className={"navigation--sandwich"}
                  onClick={() => {
                    setContactPanelRequested(false);
                    setResponsiveRequested(prev => !prev);
                  }}
                >
                  <img src={sandwich} alt={"sandwich"} />
                </button>
              )}
            </div>

            {!contactPanelRequested ? (
              <>
                <div className={"navigation--responsive--links"}>
                  <Link to={"/"} activeClassName={"responsive-active-link"}>
                    Home
                  </Link>
                  <Link
                    to={"/become-counselor"}
                    activeClassName={"responsive-active-link"}
                  >
                    Zostań doradcą
                  </Link>
                  <Link to={"/"} activeClassName={"responsive-active-link"}>
                    ICO
                  </Link>
                  <Link to={"/"} activeClassName={"responsive-active-link"}>
                    Kontakt
                  </Link>
                  <Link to={"/"} activeClassName={"responsive-active-link"}>
                    Regulamin
                  </Link>
                  <Link to={"/"} activeClassName={"responsive-active-link"}>
                    Polityka Prywatności
                  </Link>
                  <Link to={"https://unilaw-app.nebucode.com/sign-in"}>
                    Zaloguj się
                  </Link>
                </div>

                <Link className="navigation-button--link" to="/register">
                  <div
                    className="navigation-button"
                    style={{ maxWidth: 200, marginTop: 50 }}
                  >
                    <Button text={"Zarejestruj się"} />
                  </div>
                </Link>
              </>
            ) : (
              <div className={"navigation--responsive--contact"}>
                <p
                  className={"navigation--responsive--title"}
                  style={{ marginTop: 50, marginBottom: 30 }}
                >
                  Kontakt
                </p>

                <div>
                  <Info
                    text={"8819 Ohio St. South Gate, CA 90280"}
                    link={""}
                    icon={require("../../../assets/vector/mark.svg").default}
                    additionalStyle={{ marginBottom: 12 }}
                  />
                  <Info
                    text={"contact@unilaw.io"}
                    link={"mailto:contact@unilaw.io"}
                    icon={require("../../../assets/vector/message.svg").default}
                    additionalStyle={{ marginBottom: 12 }}
                  />
                  <Info
                    text={"+1 386-688-3295"}
                    link={"tel:13866883295"}
                    icon={require("../../../assets/vector/phone.svg").default}
                  />
                </div>
              </div>
            )}
          </div>

          {!contactPanelRequested ? <Copyright /> : null}
        </div>
        <button
          className={"navigation--sandwich"}
          onClick={() => setResponsiveRequested(prev => !prev)}
        >
          <img src={sandwich} alt={"sandwich"} />
        </button>
      </div>
    );
  }
};

export default RightNav;
