import "./index.scss";

import React from "react";

import { RegularText } from "../../atoms";
import { Info } from "../../molecules";

const GetInTouch = () => {
  return (
    <div className="footer-contacts--wrapper">
      <div className="footer-contacts-title">
        <RegularText text={"Get in Touch"} />
      </div>
      <div className="footer-contacts-info">
        <Info
          text={"8819 Ohio St. South Gate, CA 90280"}
          link={""}
          icon={require("../../../assets/vector/mark.svg").default}
        />
        <Info
          text={"contact@unilaw.io"}
          link={"mailto:contact@unilaw.io"}
          icon={require("../../../assets/vector/message.svg").default}
        />
        <Info
          text={"+1 386-688-3295"}
          link={"tel:13866883295"}
          icon={require("../../../assets/vector/phone.svg").default}
        />
      </div>
    </div>
  );
};

export default GetInTouch;
