import "./index.scss";

import React from "react";

import { RegularText } from "../../atoms";

const Tag = ({ icon, text }) => {
  return (
    <div className="tag--wrapper">
      <div className="tag-image--wrapper">
        <img className="tag-image" src={icon} alt="" />
      </div>
      <div className="tag-text">
        <RegularText text={text} />
      </div>
    </div>
  );
};

export default Tag;
