import "./index.scss";

import React from "react";

import { RegularText } from "../../atoms";

const CounselorText = () => {
  return (
    <div className="counselor-text--wrapper">
      <div className="counselor-text-title--wrapper">
        <h1>
          Zostań doradcą, w <span>unilaw</span>
        </h1>
      </div>
      <div className="counselor-text-subtitle">
        <RegularText
          text={
            "Zdobywaj i obsługuj klientów online. Pomagaj innym, ciesz się dodatkowym dochodem i buduj swój wizerunek eksperta."
          }
        />
      </div>
    </div>
  );
};

export default CounselorText;
