import "./index.scss";

import React from "react";

import { ProgressNumber, RegularText } from "../../atoms";

const RegisterProgressItem = ({ number, text, length, progressNumber }) => {
  return (
    <div
      className={`progress-registration-item ${
        number === 0 ? "progress-registration-item--first" : ""
      } ${number === length - 1 ? "progress-registration-item--last" : ""}`}
    >
      <div className="progress-registration-line--wrapper">
        <div className="progress-registration-number">
          <ProgressNumber
            number={`0${number + 1}`}
            active={progressNumber >= number}
          />
          <div className="progress-registration-text">
            <RegularText text={text} />
          </div>
        </div>
        {number !== length - 1 ? (
          <div
            className={`progress-registration-line ${
              progressNumber >= number + 1
                ? "progress-registration-line--active"
                : ""
            }`}
          />
        ) : (
          ""
        )}
      </div>
    </div>
  );
};

export default RegisterProgressItem;
