import "./index.scss";

import React from "react";

import { UserImage } from "../index";

const CounselorImage = () => {
  return (
    <div className="counselor-image--wrapper">
      <UserImage
        link={require("../../../assets/raster/user.png").default}
        forMain
      />
    </div>
  );
};

export default CounselorImage;
