import React, { useEffect, useRef, useState } from "react";
import { cn } from "../../../helpers/utils";
import "./index.scss";

const randomNumber = (n) => Math.random() * 2 * n - n;
const randomPositive = (n) => Math.random() * n;

const Ellipse = ({ isLight, isTurquoise, additionalStyle, animate }) => {
  const [dir, setDir] = useState({ x: randomNumber(1), y: randomNumber(2) });
  const [top, setTop] = useState(additionalStyle?.top);
  const [left, setLeft] = useState(additionalStyle?.left);
  const [parentElement, setParentElement] = useState(null);
  const ref = useRef();

  useEffect(() => {
    setParentElement(ref.current.parentElement);
  }, []);

  useEffect(() => {
    if (parentElement && animate) {
      setTop(randomPositive(parentElement.offsetHeight));
      setLeft(randomPositive(parentElement.offsetWidth));
    }
  }, [parentElement]);

  useEffect(() => {
    if (animate) {
      const interval = setInterval(() => setDir({ x: randomNumber(1), y: randomNumber(2) }), 4000);
      return () => clearInterval(interval);
    }
  }, [animate]);

  useEffect(() => {
    if (parentElement && animate) {
      setTop((top) => {
        if (top + 100 * dir.y + additionalStyle.height / 2 > parentElement.offsetHeight)
          return Math.min(top + 100 * dir.y, top - 100 * dir.y);
        else if (top + 100 * dir.y - additionalStyle.height / 2 < 0)
          return Math.max(top + 100 * dir.y, top - 100 * dir.y);
        else return top + 100 * dir.y;
      });
      setLeft((left) => {
        if (left + 100 * dir.x + additionalStyle.width / 2 > parentElement.offsetWidth)
          return Math.min(left + 100 * dir.x, left - 100 * dir.x);
        else if (left + 100 * dir.x - additionalStyle.width / 2 < 0)
          return Math.max(left + 100 * dir.x, left - 100 * dir.x);
        else return left + 100 * dir.x;
      });
    }
  }, [dir, parentElement, additionalStyle, animate]);

  return (
    <div
      ref={ref}
      className={cn(
        "ellipse",
        isLight && "ellipse--light",
        isTurquoise && "ellipse--turquoise",
        animate && "ellipse--animated"
      )}
      style={{ ...additionalStyle, top, left, transition: animate ? "4s ease-in-out" : "" }}
    />
  );
};

export default Ellipse;
