import "./index.scss";

import React from "react";

import { useWindowDimensions } from "../../../helpers/hooks";
import { LogoText, RegularText } from "../../atoms";

const Logo = ({ isWhite, withoutText }) => {
  const { height, width } = useWindowDimensions();

  return (
    <div className="logo--wrapper">
      <LogoText isWhite={isWhite} />
      {!withoutText ? (
        <div>
          <RegularText
            text={"decentralized advisory group"}
            additionalStyle={{ color: isWhite ? "#FFF" : "#000" }}
          />
        </div>
      ) : null}
    </div>
  );
};

export default Logo;
