import "./index.scss";

import React from "react";

import { Link } from "gatsby";

import { RegularText } from "../../atoms";

const NavItemDark = ({ text, link }) => {
  return !link.includes("http") ? (
    <div className="nav-link-dark--wrapper">
      <Link
        className="nav-link-dark"
        activeClassName={"nav-link-dark--active"}
        to={link}
        exact
      >
        <RegularText text={text} />
      </Link>
    </div>
  ) : (
    <div className="nav-link-dark--wrapper">
      <a className="nav-link-dark" href={link} target="_blank">
        <RegularText text={text} />
      </a>
    </div>
  );
};

export default NavItemDark;
