import "./index.scss";

import React from "react";

import { Icon, RegularText } from "../../atoms";

const BackButton = ({ handleOnClick, isDark }) => {
  return (
    <div className="back-button--wrapper" onClick={handleOnClick}>
      <div className="back-button-icon">
        <Icon
          link={
            isDark
              ? require("../../../assets/vector/dark-arrow-back.svg").default
              : require("../../../assets/vector/arrow-back.svg").default
          }
        />
      </div>
      {!isDark ? (
        <div className="back-button-text">
          <RegularText text={"Wróć"} />
        </div>
      ) : null}
    </div>
  );
};

export default BackButton;
