import "./index.scss";

import React from "react";

import { Icon, RegularText } from "../../atoms";

const Card = ({ link, text }) => {
  return (
    <div className="card--wrapper">
      <div className="card-icon">
        <Icon link={link} />
      </div>
      <div className="card-text">
        <RegularText text={text} />
      </div>
    </div>
  );
};

export default Card;
