import "./index.scss";

import React from "react";

import { Ellipse } from "../../atoms";
import Tag from "../../molecules/Tag";
import { cn } from "../../../helpers/utils";

const UserImage = ({ link, isRight, forMain }) => {
  return (
    <div className="user--wrapper">
      <div className="user-image--wrapper">
        <img
          className={cn("user-image", forMain && "user-image--for-main")}
          src={link}
          alt=""
        />

        {forMain ? (
          <>
            <div className="counselor-image-tag counselor-image-tag--left-top">
              <Tag
                text={"Szybka pomoc"}
                icon={require("../../../assets/vector/clock.svg").default}
              />
            </div>
            <div className="counselor-image-tag counselor-image-tag--right-top">
              <Tag
                text={"Nie pobieramy opłat"}
                icon={require("../../../assets/vector/money.svg").default}
              />
            </div>
            <div className="counselor-image-tag counselor-image-tag--left-bottom">
              <Tag
                text={"Dobra opinia"}
                icon={require("../../../assets/vector/like.svg").default}
              />
            </div>
            <div className="counselor-image-tag counselor-image-tag--right-bottom">
              <Tag
                text={"100% Gwarancja"}
                icon={require("../../../assets/vector/checkMark.svg").default}
              />
            </div>
          </>
        ) : null}

        <div
          className={`${
            isRight ? "" : `user-ellipse--left${forMain ? "--for-main" : ""}`
          } user-ellipse`}
        >
          <Ellipse />
        </div>
        <div className={`user-ellipse--front${forMain ? "--for-main" : ""}`}>
          <Ellipse />
        </div>
      </div>
    </div>
  );
};

export default UserImage;
