import "./index.scss";

import React from "react";

import { navigate } from "gatsby";

import backResponsiveIcon from "../../../assets/vector/back-responsive-icon.svg";
import { BoldLacaText } from "../../atoms";
import { BackButton } from "../../molecules";

const StepBackBlock = ({ title, handleOnClickBack }) => {
  return (
    <div className="step-back-header--wrapper">
      <button
        type={"button"}
        className={"step-back-header--button-responsive"}
        onClick={
          handleOnClickBack
            ? handleOnClickBack
            : () => {
                navigate(-1);
              }
        }
      >
        <img src={backResponsiveIcon} alt={"back"} width={40} />
      </button>

      <div className="step-back-header-button">
        <BackButton
          handleOnClick={
            handleOnClickBack
              ? handleOnClickBack
              : () => {
                  navigate(-1);
                }
          }
        />
      </div>
      <div className="step-back-header-text">
        <BoldLacaText text={title} />
      </div>
    </div>
  );
};

export default StepBackBlock;
